import React, { useState, useContext } from "react";
import { MyContext } from "./Context";
import image3 from "./Video/image3.svg"

function Profile() {
  const { details, current } = useContext(MyContext);

  return (
    <div className="profile">

      <div className="left2">
     { current.length===0?  <div className="card"> <h2>No user found, please log in to view your profile</h2> </div> : 
        <div className="card">
          {current.map((e) => (
            <>
              <div className="cardflex2">
                <p>Name</p>
                <h1>{e.name}</h1>
              </div>
              <div className="div">
                <p>Email</p>
                <h1>{e.email}</h1>
              </div>
              <div className="cardflex">
                <div>  <p>Class</p>
                <h1>{e.classnum}</h1>
                   </div>
                <div> <p>Contact</p>
                <h1>{e.phone}</h1></div>
              </div>
            </>
          ))}
        </div>}
      </div>  
      <img src={image3} alt="" srcset="" />
    </div>
  )
}

export default Profile;
