import React, { useState, useContext, useEffect } from "react";
import { Link } from "react-router-dom";
import {motion} from "framer-motion"
import { MyContext } from "./Context";
import {HiMenuAlt3} from "react-icons/hi"
import {BiMenuAltLeft} from "react-icons/bi"

function Nav() {
  const{logged,setlogged,setcurrent}=useContext(MyContext)
  const [flag, setflag] = useState(false);
  const[show,setshow]=useState(false)

  const click=()=>{
    setlogged(false);
    setcurrent([])
    setshow(!show)
  }

const change=()=>{
  setshow(!show)
}

  const scroll = () => {
    if (window.scrollY > 100) {
      setflag(true);
      console.log("I am inside 500");
    } else if (window.scrollY < 100) {
      setflag(false);
      console.log("I am inside 1000");
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", scroll);
    return () => window.removeEventListener("scroll", scroll);
  }, []);
  return (
    <>
      <motion.nav className={flag ? "nav2" : "nav"} initial={{opacity:0,translateY:-200}} animate={{opacity:1,translateY:0}} transition={{duration:2, delay:0.1, staggerChildren: 0.1,
                delayChildren: 0.3,}}>
        <img
          src="https://www.schooglink.com/_next/static/images/logo-95ce09074fb484aceabb18e66b50190c.png"
          alt=""
        />
         <HiMenuAlt3 className="openmenu" onClick={change}/>

        <ul className={show?"Nav":"no-nav"} >
        <BiMenuAltLeft className="closemenu" onClick={change}/>
         <motion.li> <Link to="/" onClick={()=>setshow(!show)}>Home</Link> </motion.li>
         <motion.li> <Link to="/register" onClick={()=>setshow(!show)}>Register</Link> </motion.li>
       <li>{logged?<Link to="/login" onClick={click} >Log out</Link>:<Link to="/login" onClick={()=>setshow(!show)}>Log in</Link>}</li>  
          <li><Link to="/profile" onClick={()=>setshow(!show)}>Profile</Link></li>
        </ul>
      </motion.nav>
    </>
  );
}

export default Nav;
